import commFooter from '../../components/commFooter/commFooter.vue'
export default{
	data(){	
		return{
			userIcon:"",
			nickName:"",
			phone:"",
			maskShow:false
		}
	},
	methods:{
		gotoOrder:function(){
			uni.navigateTo({
				url:"../order/orderList"
			});
		},
		gotoAddr:function(){
			uni.navigateTo({
				url:"../addr/addr"
			});
		},
		gotoReturnOrder:function(){
			uni.navigateTo({
				url:"../order/returnOrders"
			});
		},
		gotoFeedback:function(){
			uni.navigateTo({
				url:"../feedback/feedback"
			});
		},
		gotoCollection:function(){
			uni.navigateTo({
				url:"../collection/myCollection"
			});
		},
		logOut:function(){
			this.maskShow=true;
		},
		cancelLogout:function(){
			this.maskShow=false;
		},
		confirmLogout:function(){
			localStorage.removeItem('token');
			localStorage.removeItem('userId');
			localStorage.removeItem('phone');
			localStorage.removeItem('icon');
			localStorage.removeItem('shopCode');
			localStorage.removeItem('shopName');
			localStorage.removeItem('user');
			uni.navigateTo({
				url:"../index/index"
			})
		}
	},
	onLoad:function(){
		this.nickName=uni.getStorageSync("user");
		this.phone=uni.getStorageSync("phone");
		this.userIcon=this.commonConfig.contentPic+uni.getStorageSync("icon");
	},
	components:{
		commFooter
	}
}
